import { add, remove, update } from './base'

let config = {}

let updateConfig = function (data) {
  config.type = data.type ? data.type : config.type
  config.color = data.color ? data.color : config.color
  config.height = data.height ? data.height : config.height
}

export default {
  //  0 
  start(options) {
    return add(
      Yox.object.extend(
        {},
        options,
        config
      )
    )
  },
  // 
  finish() {
    update({
      percent: 100
    })
    setTimeout(
      function () {
        return remove()
      },
      1000
    )
  },
  // 
  update(data) {
    return update(data)
  },
  config(data) {
    updateConfig(data)
  },
  destroy() {
    config = {}
    element.remove()
  }
}